#intro {
    height: calc(100vh - 4rem);
    width: 100v;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    /* padding: 1rem; */
}

.photo {
    position: absolute;
    top: -1rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 80%;
    /* width: 33%; */
    width: auto;
    margin-top: 4rem;
    margin-right: 7rem;
    border-radius: 50%;
    border: 3px solid wheat;
    box-shadow: rgb(0, 0, 0) 0px 30px 90px;   
}

.introContent {
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello {
    font-size: 1.75rem;
    font-weight: 100;
}

.introName {
    color: yellow;
}

.introPara {
    font-size: medium;
    font-weight: 200;
    /* letter-spacing: 1px; */

}

.btnImg {
    height: 1.2rem;
    object-fit: cover;
    margin: 0.1 0.25rem;
    width: 2rem;

}

.btn {
    background-color:teal;
    color: white;
    margin: 1rem;
    padding: 5px;
    border: none;
    border-radius: 0.5rem;
    font-weight: bold;
    transition: color .3s ease-in-out,
    box-shadow .3s ease-in-out;
    font-size: large;
    border: 2px dashed rgb(255, 225, 0);

}

.btn:hover {
    color: #fff;
    box-shadow: inset 200px 0 0 0 #b55454;
    border: #fff;
}

.helloimg {
    height: 50px;
    width: 50px;
}

@media screen and (max-width:840px) {
    .bg {
        right: -10vw;
    }

    .introContent {
        font-size: 10vw;
    }

    .hello {
        font-size: 4.5vw;
    }
}

@media screen and (min-width:480px) {
    .bg {
        right: -20vw;
    }
}