#about{
    overflow: hidden;
    width: 100vw;
    max-width:65rem;
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;

}
.aboutDiv{
    margin-left: 5rem;
}
.aboutTitle{
    font-size: 3rem;
    font-weight: 100;
    /* padding-top: 3rem; */
    margin-bottom: 1.5rem;
    display: flex;
    margin-left: 7rem;
    /* border-bottom: 2px solid yellow; */
    color: #c1c110;
    
    
}
.aboutMain{
    font-weight: 400;
    font-size: 0.9rem;
    max-height: 51rem;
    /* padding: 0 2rem; */
    /* margin-left: 10rem; */
    /* padding-left: 5rem; */
}
.aboutImg{
    object-fit: cover;
    height: 80vh;
    width: 70vw;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}
@media screen and (max-width:720px) {
    .aboutTitle{
        font-size: 2vh;
    }
    
    .aboutMain{
        font-size: 2vh;
    }

}
@media screen and (max-width:420px){
    .aboutTitle{
        font-size: 2vh;
    }
    
    .aboutMain{
        font-size: 2vh;
    }
}