.navbar{
    height: 5rem;
    width: 100vw;
    min-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: -webkit-sticky;
    position:sticky;
    z-index: 3;
    top: 0;
    background-color: rgb(37, 36, 36);
}
.logo{
    object-fit: cover;
    height: 3rem;
    width: 10rem;
    background: none;
}
.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
}
.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.desktopMenuBtn{
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    transition: color .3s ease-in-out,
    box-shadow .3s ease-in-out;
    
}
.desktopMenuBtn:hover{
    color: #fff;
    box-shadow: inset 200px 0 0 0 #b55454;
    border: #fff;
}
.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;

}
.active{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
    /* height: 5rem; */
    /* width: 5rem; */
}
.mobMenu{
  display: none;
  object-fit: cover;
  height: 1.8rem;
}
.listItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
    cursor: pointer;
}

