.skillHead{
    text-align: center;
    margin-top: 3rem;
    margin-bottom: 4rem;
    font-weight: bold;
    font-size:xx-large;
    /* border-bottom: 2px solid red; */
    color: #c1c110;
}
.skillBar{
   display: flex;
   margin: 1rem;
   background-color: rgb(34, 32, 5);
   padding: 0.5rem;
   border-radius: 0.2rem;
   /* margin-left: 3rem; */
   padding-left: 1rem;
   
   
}
.skillBar:hover{
  --c: #e6e617; 
  --b: .1em;    
  --d: 20px;   
  
  --_s: calc(var(--d) + var(--b));
  
  color: var(--c);
  border: solid #0000;
  border-width: var(--b) var(--b) var(--_s) var(--_s);
  background:
    conic-gradient(at left var(--d)  bottom var(--d),
      #0000 90deg, rgb(255 255 255 /0.3) 0 225deg,rgb(255 255 255 /0.6) 0) border-box,
    conic-gradient(at left var(--_s) bottom var(--_s),
      #0000 90deg,var(--c) 0) 0 100%/calc(100% - var(--b)) calc(100% - var(--b))  border-box;
  transform: translate(calc(var(--d)/-1),var(--d));
  clip-path: 
    polygon(
     var(--d) 0%, 
     var(--d) 0%, 
     100% 0%, 
     100% calc(100% - var(--d)),
     100% calc(100% - var(--d)),
     var(--d) calc(100% - var(--d))
    );
  transition: 0.5s;
  transform: translate(0,0);
  clip-path: 
    polygon(
     0% var(--d), 
     var(--d) 0%, 
     100% 0%, 
     100% calc(100% - var(--d)), 
     calc(100% - var(--d)) 100%, 
     0% 100%
   );
}
.skillMain{
    display: grid;
    /* flex-direction: calc(3 1fr); */
    grid-template-columns: repeat(3,1fr);
    padding: 0 2rem 2rem;
    margin-bottom: 2rem;
}
.skillImg{
    height: 8vh;
    width: 4vw;
    border-radius: 0.5rem;
    margin-top: 5px;
}